// app/cadastro/page.tsx
'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Image from 'next/image';
import '../../styles/login.css';

export default function CadastroPage() {
  const router = useRouter();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  const handleRegister = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // Aqui você pode adicionar a lógica de cadastro (validação, chamadas API, etc.)
    console.log('Nome:', name);
    console.log('Email:', email);
    console.log('Senha:', password);
    console.log('Confirmar Senha:', confirmPassword);
  };

  return (
    <main className="login-page">
      <div className="main-grid">
        <div className="logo-container">
          {/* Logo / imagem grande à esquerda, conforme layout */}
        </div>

        <div className="form-container">
          <div className="form-wrapper">
            <h2>Criar Conta</h2>

            <form onSubmit={handleRegister}>
              <div className="input-group">
                <label htmlFor="name">Nome</label>
                <input
                  type="text"
                  id="name"
                  placeholder="Seu nome completo"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required
                />
              </div>

              <div className="input-group">
                <label htmlFor="email">Email</label>
                <input
                  type="email"
                  id="email"
                  placeholder="seuemail@email.com"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>

              <div className="input-group">
                <label htmlFor="password">Senha</label>
                <input
                  type="password"
                  id="password"
                  placeholder="Crie uma senha forte"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </div>

              <div className="input-group">
                <label htmlFor="confirmPassword">Confirmar Senha</label>
                <input
                  type="password"
                  id="confirmPassword"
                  placeholder="Repita a senha"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  required
                />
              </div>

              <button type="submit" className="login-button">
                Cadastrar
              </button>
            </form>

            <div className="separator">OU</div>

            <button type="button" className="google-login-button">
              <Image
                src="/icons/google.svg"
                alt="Logo do Google"
                width={20}
                height={20}
                className="google-icon"
              />
              Cadastrar com o Google
            </button>

            <div className="signup-link">
              <p>
                Já possui uma conta?{' '}
                <Link href="/login">Faça Login</Link>
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="footer-credits">
        <p>Projeto Integrado I - IFSP PEP</p>
        <p>Pedro Lucas, Caique, Erick, Gustavo</p>
      </div>
    </main>
  );
}
